import "../document.css";
import React from "react";
export default () => (
  <div style={{ padding: "1rem" }}>
    <p>
      This user agreement will be effective for all users as of May 30, 2020.
    </p>

    <h3>Welcome to ZIME LV!</h3>

    <p>
      These are the terms and conditions of the contract between you and ZIME.LV
      (“ZIME”) governing your use of your ZIME account and the ZIME services,
      which we call our user agreement. It applies to ZIME accounts worldwide.
    </p>
    <p>
      To use the ZIME services you need to open a ZIME account. By opening and
      using a ZIME account, you agree to comply with all of the terms and
      conditions in this user agreement.
    </p>

    <p>
      Please read carefully all of the terms and conditions of this user
      agreement and each of the other documents that apply to you.
    </p>
    <p>
      We may revise this user agreement and any of the documents listed above
      from time to time. The revised version will be effective at the time we
      post it, unless otherwise noted. If our changes reduce your rights or
      increase your responsibilities we will post a notice on the Policy Updates
      page of our website and provide notice to you of at least 60 days. By
      continuing to use our services after any changes to this user agreement
      take effect, you agree to be bound by those changes. If you do not agree
      with any changes, you may close your account before the changes take
      effect.
    </p>

    <h4>Information about us and our service</h4>
    <p>
      ZIME’s main business is the issuance of local currencies and the provision
      of payment services using that local currencies.
    </p>
    <p>
      For more information about us and our service, you can read our Key
      Payment and Service Information.
    </p>
    <p>
      This user agreement will be made available to you at all times on the ZIME
      website.
    </p>

    <h3>Opening an account</h3>

    <p>We allow no more than one account per individual.</p>

    <p>
      We offer three types of accounts: current accounts, credit accounts and
      savings accounts.
    </p>

    <h5>Current accounts</h5>

    <p>
      With a current account you can send and receive local currencies from
      other ZIME users and pay online for purchases denominated in local
      currencies.
    </p>

    <h5>Credit accounts</h5>

    <p>
      With a credit account you can send local currencies to other local
      currency market participants and pay online for purchases denominated in
      local currencies.
    </p>

    <h5>Savings accounts</h5>

    <p>
      With a savings account you can send local currencies to other ZIME users
      and pay online for purchases denominated in local currencies.
    </p>

    <p>
      A daily allowance payment of Z 48 is dispatched to the savings account.
    </p>

    <h5>Safe use of your ZIME account</h5>

    <p>
      You should take reasonable steps to stop your ZIME account being misused.
      You must maintain adequate security and control of any and all devices,
      items, IDs, passwords and personal identification numbers / codes that you
      use to access your ZIME account and the ZIME services.
    </p>

    <p>
      You must comply with all reasonable instructions we may issue regarding
      how you can keep your local payment instrument safe.
    </p>

    <p>
      We may require you to authenticate any instruction relating to your
      account (i.e. give us the information that we need to be sure that it’s
      you giving us the instruction, such as submitting your correct log-in
      information – this could include your e-mail address and password) and
      otherwise successfully log into your ZIME account to provide to us your
      instruction.
    </p>

    <p>
      You must keep your postal address, email address and other contact
      information current in your ZIME account profile.
    </p>

    <p>
      You may expressly grant, remove and manage permissions for some third
      parties to take certain actions on your behalf. In some cases you can do
      this when logged into your account – in other cases you can do this
      directly with the third party. You acknowledge that if you grant
      permission for a third party to take actions on your behalf, we may
      disclose certain information about your Account to this third party.
    </p>

    <p>
      You may permit third party service providers licensed by applicable law
      to:
    </p>

    <ul>
      <li>
        provide account information services to access information about your
        account on your behalf;
      </li>
      <li>
        confirm whether an amount necessary for the execution of a payment
        transaction is available on your account; or
      </li>
      <li>
        provide payment initiation services to initiate payments from your
        account on your behalf.
      </li>
    </ul>

    <p>
      Granting permission to any third party to access your account in any way
      does not relieve you of any of your responsibilities under this user
      agreement. You are liable to us for the actions that you authorise the
      third parties to carry out. You will not hold us responsible for, and you
      will indemnify us from, any liability arising from the actions or
      inactions of such third parties in connection with the permissions you
      granted, subject to your mandatory legal rights.
    </p>

    <h3>Closing Your ZIME Account</h3>

    <p>
      We may close your account at our convenience by providing you with two
      months’ prior notice. We may also close your account at any time if:
    </p>

    <ol>
      <li>
        you are in breach of this user agreement and/or we are otherwise
        entitled to close your account under this user agreement;
      </li>
      <li>you do not access your account for three years; or</li>
      <li>
        we suspect that your account has been accessed without your
        authorisation.
      </li>
    </ol>

    <p>
      Where we decide to close your account we will provide you with notice of
      account closure and where practicable, the reasons for closing your
      account, together with the ability to withdraw any undisputed funds that
      we are holding.
    </p>

    <p>When your account is closed:</p>
    <ul>
      <li>
        this user agreement terminates immediately, except that this user
        agreement survives termination to the extent and for so long as we
        require to deal with the closure of your account and to comply with
        applicable laws and regulations;
      </li>
      <li>
        we may cancel any pending transactions and you will forfeit any balances
        associated with special funding arrangements;
      </li>
      <li>
        we may suspend, limit or terminate your access to or use of our
        services, websites, software, systems (including any networks and
        servers used to provide any of the services) operated by us or on our
        behalf or some or all of the services;
      </li>
      <li>
        you will remain liable for all outstanding obligations under this user
        agreement related to your account prior to closure;
      </li>
      <li>
        we may keep your account information in our database for the purpose of
        fulfilling our legal obligations; and
      </li>
      <li>
        we may retain your electronic currency after closure to the extent and
        for the time we reasonably require to protect us and/or any third party
        against the risk of reversals, fees, fines, penalties and other
        liabilities of whatever nature. After this time you will be able to
        withdraw any undisputed funds that we are holding. Please Contact Us if
        you have any questions about funds held in your account on closure.
        <strong>
          If you are the legal representative of an incapacitated or deceased
          account holder, please Contact Us for assistance.
        </strong>
      </li>
    </ul>

    <p>
      In the event that we decide to discontinue any of our services or any
      portion or feature of our services for any reason, we will give you at
      least 60 days’ prior notice before discontinuing the service or feature,
      unless we determine in good faith that:
    </p>
    <ul>
      <li>
        the service or feature must be discontinued sooner as required by law or
        a third-party relationship; or
      </li>
      <li>
        doing so could create a security risk or substantial economic or
        material technical burden.
      </li>
    </ul>

    <h3>Managing Multiple Local Currencies</h3>

    <p>
      When you open your account, it is configured for use with the opening
      currency of your account.
    </p>

    <p>The opening currency of your account is Zime (Z).</p>

    <p>
      You may configure your account further so that you can use it to store
      local currency and send and receive payments in local currencies other
      than the opening currency of your account. If you hold a ZIME balance, we
      may allow you to convert it to balance in another currency.
    </p>

    <p>
      If your ZIME balance is not enough to cover the amount of a payment you
      instruct us to make in a particular currency, we may perform a currency
      conversion from any ZIME balance in another currency to cover the
      shortfall. There may be some restrictions on where you can send payments
      in certain local currencies.
    </p>

    <p>
      We may allow you to choose the way in which your account treats and/or
      converts payments received in local currencies other than the opening
      currency of your account. To receive transfer in a currency other than the
      opening currency of your account, it may be necessary to create a balance
      in that currency or convert the transfer into another currency that we
      allow you to hold.
    </p>

    <p>
      Payments in certain local currencies can only be received by automatic
      conversion of the transfer into another currency that we allow you to
      hold.
    </p>

    <p>
      We may, at our discretion, impose limits on the amount of local currency
      you can convert or the number of conversions you can perform. You may not
      manage or convert currencies for speculative trading purposes, conversion
      arbitrage, conversion options, or any other activity that we determine is
      primarily for the purpose of gaining or making profit based on currency
      conversion rates. We may hold, cancel, or reverse any transaction we
      determine to violate this policy.
    </p>
  </div>
);
